.page-header {
  @media (min-width: @tablet-width) {
    background-image: linear-gradient(@basic-white 89px, @header-line-grey 89px, @header-line-grey 90px, @header-bg-color 90px);

    &--index {
      background-image: linear-gradient(@basic-white 89px, @header-line-grey 89px, @header-line-grey 90px, @hero-bg-color 90px);
    }
  }
}
